import React from 'react';
import PropTypes from 'prop-types';
import get from 'lodash/get';

import APIcon from '../common/APIcon/APIcon';
import config from '../../config';
import { getBEMClasses } from '../../helpers/cssClassesHelper';
import './PageFooterComponent.css';

const pageFooterClasses = getBEMClasses('page-footer');

const PageFooterComponent = (props, context) => {
  return (
    <footer className={pageFooterClasses()}>
      <div className={pageFooterClasses('footer-left-content')}>
        {context.t('{n}™ {y}. All Rights Reserved.', {
          n: config.global.siteName,
          y: new Date().getFullYear()
        })}
      </div>
      <div className={pageFooterClasses('footer-right-content')}>
        {get(config, 'Footer.links', []).map((column, idx) => (
          <div key={idx} className={pageFooterClasses('text')}>
            {column.map((item, innerIdx) => (
              <a
                target={config.Footer.openInNewWindow ? '_blank' : ''}
                rel={config.Footer.openInNewWindow ? 'noopener noreferrer' : ''}
                key={`${idx}${innerIdx}`}
                href={item.link}>
                {context.t(item.text)}
              </a>
            ))}
          </div>
        ))}
        <div className={pageFooterClasses('icon')}>
          {!config.Footer.powerOff && <APIcon name="logo-powered" />}
        </div>
      </div>
    </footer>
  );
};

PageFooterComponent.contextTypes = {
  t: PropTypes.func.isRequired
};

export default PageFooterComponent;



